.main {
    width: 100%;
    height: calc(100vh - 60px);
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    overflow: hidden;
    @include title-tablet;

    .projects {
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;     
    }    


    .code{
        height: 100vh;
        filter: drop-shadow(0 20px 60px rgba(0, 0, 0, 0.16));
        }

    .main__image-projects{
        width: 100%;
        height: auto;
    }
}
    
    .main__image-projects{
        width: 100%;
        height: auto;
        &:hover{
         
            transform: rotate(+10deg);
            -ms-transform:rotate(+10deg); // IE 9 
            -moz-transform:rotate(+10deg); // Firefox 
            -webkit-transform:rotate(+10deg); // Safari and Chrome 
            -o-transform:rotate(+10deg); // Opera

            transition: 0.3s;
            -webkit-transition: 0.3s; // IE 9
            -moz-transition: 0.3s; // Firefox
            -ms-transition: 0.3s; // Safari and Chrome 
            -o-transition: 0.3s; // Opera 
           
    }

}

@media (min-width: 768px) {
    .main{
        @include title-pc;
        padding: 5px;
        padding-top: 20px;


        .projects {
            display:flex;
            flex-direction: row-reverse;
            padding: 10px;

        }
        .code{
            height: calc(100vh - 80px);
            filter: drop-shadow(0 20px 60px rgba(0, 0, 0, 0.16));
            }

        .main__image-projects{
            width: 100%;
            height: auto;
        }
    }
  }
  
  @media (min-width: 1200px) {
    .main{
        @include title-pc;
        .code{
            height: calc(100vh - 80px);
            filter: drop-shadow(0 20px 60px rgba(0, 0, 0, 0.16));
            }
        .main__image-projects{
            width: auto;
            height: calc(100vh - 130px);
        }
    }
  }